@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Inter:wght@300;400;500;600;700&display=swap");
/********************TOP-BAR********************/
.top-bar {
  background-color: #f8f9fc;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1040;
}

.topbar-social-menu .social-link {
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  color: #314047;
  transition: all 0.5s ease;
}
.topbar-social-menu .social-link:hover {
  color: #048565;
}

.language-switch .btn:focus {
  box-shadow: none;
}

/**********************NAVBAR***********************/
.navbar {
  transition: all 0.5s ease;
  padding: 0;
  margin-top: 44px;
  background-color: #fff;
  box-shadow: 0px 3px 10px 0px rgba(49, 64, 71, 0.08);
}
.navbar .navbar-nav .nav-link {
  color: #314047;
  font-size: 13px;
  background-color: transparent;
  padding: 23px 18px;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
  font-weight: 600;
}
.navbar .navbar-nav .nav-link:hover, .navbar .navbar-nav .nav-link:active, .navbar .navbar-nav .nav-link.active {
  color: #02af74;
}
.navbar .navbar-toggler {
  padding: 0px;
  border: none;
  font-size: 24px;
}
.navbar .navbar-toggler:focus {
  box-shadow: none;
}
.navbar .header-item {
  color: #314047;
  transition: all 0.5s ease;
}

.navbar-brand .logo-dark {
  display: inline-block;
}
.navbar-brand .logo-light {
  display: none;
}

.header-item {
  height: 65px;
  line-height: 65px;
  border: 0;
  border-radius: 0;
  display: inline-block;
}

.notification-wrapper {
  max-height: 250px;
  overflow: hidden scroll;
}
.notification-wrapper .notification-item {
  padding: 15px;
  transition: all 0.5s ease;
}
.notification-wrapper .notification-item:hover {
  background-color: #f8f9fc;
}

.notification-header {
  padding: 1rem;
}

.notification-footer {
  padding: 10px;
}

.noti-icon .count {
  position: absolute;
  top: 15px;
  right: -6px;
  margin-top: -3px;
  margin-right: -5px;
  width: 21px;
  height: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  background-color: #da3746;
  border-radius: 40px;
  color: #fff;
  border: 2px solid #fff;
}

.nav-sticky.navbar {
  background: #fff;
  margin-top: 0px;
  box-shadow: 0px 3px 10px 0px rgba(49, 64, 71, 0.08);
}
.nav-sticky .header-item {
  color: #314047;
}

@media (max-width: 991.98px) {
  .navbar .navbar-nav .nav-item .nav-link {
    padding: 8px 15px;
  }

  .navbar-collapse {
    max-height: 385px;
    overflow-y: auto;
    padding: 0;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 65px;
    background: #fff;
    padding-bottom: 10px;
    padding-left: 25px;
    box-shadow: 0px 3px 10px 0px rgba(49, 64, 71, 0.08);
  }
}
.arrow-down {
  display: inline-block;
}
.arrow-down:after {
  border: 0;
  left: auto;
  content: "\f0140";
  font-family: "Material Design Icons";
  top: 35%;
  right: 28px;
  font-size: 14px;
  margin-left: 2px;
  transform: translateY(-50%);
  transition: all 0.3s ease-out;
}
@media (max-width: 991.98px) {
  .arrow-down::after {
    right: 30px;
    position: absolute;
  }
}

@media (max-width: 767.98px) {
  .navbar {
    margin-top: 0px;
  }

  .top-bar {
    display: none;
  }
}
