#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 99999;
  transition: all 0.5s ease;
  visibility: visible;
  opacity: 1;
}
#preloader #status {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

#status {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(2, 175, 116, 0.15);
}
#status ul {
  display: flex;
}
#status ul li {
  list-style: none;
  height: 20px;
  width: 20px;
  background-color: #02af74;
  margin: 0 4px;
  border-radius: 50%;
  animation: bounce 1s linear infinite;
}
#status ul li:nth-child(1) {
  animation-delay: 0.2s;
  background-color: #013120;
}
#status ul li:nth-child(2) {
  animation-delay: 0.6s;
  background-color: #014a31;
}
#status ul li:nth-child(3) {
  animation-delay: 0.1s;
  background-color: #016342;
}
#status ul li:nth-child(4) {
  animation-delay: 0.8s;
  background-color: #017d53;
}
#status ul li:nth-child(5) {
  animation-delay: 0.5s;
  background-color: #029663;
}
#status ul li:nth-child(6) {
  animation-delay: 0.1s;
  background-color: #02af74;
}
@keyframes bounce {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(0px);
  }
}

